.footer {
    background-color: #f0f0f0;
    padding: 10px;
    font-size: 14px;
    text-align: center;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 98%;
    color: black;
}

.footer span {
    display: inline-block;
    margin-right: 10px;
}

.footer span:first-child {
    margin-left: 10px;
}

.footer span:last-child {
    margin-right: 0;
    float: right;
}